.worldMap {
  @apply min-w-full w-fit min-h-screen xl:w-[1350px] xl:h-[900px] bg-gray-700;
  
  svg {
    width: 1100px;
    @apply overflow-visible;
    @apply p-10;
  }

  path {
    fill: #818181;
    fill-opacity: 1;
    stroke: #505050;
    stop-opacity: 1;
    stroke-width: 0.4;
  
    &:hover {
      @apply cursor-pointer;
      fill: #aaa;
    }
  }

  .visited {
    @apply fill-emerald-500;
  }

  g {
    @apply xl:scale-125;
  }
}

